<template>
	<view>
		<view class="tips">
			<view class="lefttop"></view>
			<view class="leftbottom"></view>
			<view class="tipsinit">
				<view>在这里你可以添加每天重复出现的记账项，设置完成后可直接从收支明细中填入金额。</view>
			</view>
		</view>
		
		<view class="preset">
			<view class="presetdetail" v-for="(item,index) in presetlist" :key="index">
				icon {{item}}
			</view>
		</view>
		
		<view class="add-detail-button-wrapper">
		  <view class="add-detail-button" @click="addDetail">
		    <view class="detail-row"></view>
		    <view class="detail-column"></view>
		  </view>
		</view>
		
		<view class="add-detail-wrapper" v-show="showAddDetail">
		  <view class="cancel-add-detail" @click="cancelAddDetail"></view>
		  <view :class="addDetailClass"> </view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				presetlist:["餐饮","日常"],
				addDetailClass: ['add-detail'],
				showAddDetail: false,
			}
		},
		methods: {
			addDetail() {
			
			  this.showAddDetail = true;
			  if (this.addDetailClass.length === 1) {
			    this.addDetailClass.push('slide-top');
			  } else {
			    this.addDetailClass.pop();
			    this.addDetailClass.push('slide-top');
			  }
			},
			cancelAddDetail() {
			  setTimeout(() => {
			    this.showAddDetail = false;
			  }, 501);
			  if (this.addDetailClass.length === 1) {
			    this.addDetailClass.push('slide-bottom');
			  } else {
			    this.addDetailClass.pop();
			    this.addDetailClass.push('slide-bottom');
			  }
			}
		}
	}
</script>

<style>
	.lefttop{
		width: 10rpx;
		height: 120rpx;
		background-color: grey;
		position:absolute; 
		top:20rpx; 
		left:100rpx; 
		z-index:9;
		transform: rotate(45deg);      
		-o-transform:rotate(45deg);    
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);  
	}
	.leftbottom{
		width: 10rpx;
		height: 120rpx;
		background-color: grey;
		position:absolute; 
		top:100rpx; 
		left:100rpx; 
		z-index:9;
		transform: rotate(135deg);      
		-o-transform:rotate(135deg);    
		-webkit-transform:rotate(135deg);
		-moz-transform:rotate(135deg); 
	}
	.tipsinit{
		height: 153rpx;
		width: 500rpx;
		/* background-color: blue; */
		border-top: grey 10rpx solid;
		border-bottom:grey 10rpx solid;
		border-right: grey 10rpx solid;
		position: relative;
		top:36rpx;
		left: 145rpx;
	}
	.tipsinit view{
		margin-top: 10rpx;
	}
	.preset{
		margin-top: 100rpx;
		font-size: 40rpx;
	}
	.preset .presetdetail{
		height: 70rpx;
		width: 600rpx;
		background-color: rgba(128,128,128,0.6);
		/* margin-bottom: 20rpx; */
		margin: 0 auto;
		margin-bottom: 20rpx;
		padding-left: 20rpx;
		line-height: 70rpx;
		/* vertical-align: 70rpx; */
	}
	
	
	.add-detail-button-wrapper {
	  width: 100%;
	  height: 150rpx;
	  position: fixed;
	  left: 0;
	  bottom: 0;
	  z-index: 8;
	}
	
	.add-detail-button {
	  background-color: rgb(16, 16, 16);
	  border-radius: 30px;
	  width: 100rpx;
	  height: 100rpx;
	  margin: 20rpx auto;
	  position: relative;
	}
	
	.add-detail-button .detail-column {
	  height: 70rpx;
	  width: 10rpx;
	  background-color: #fff;
	  position: absolute;
	  left: 44rpx;
	  top: 14rpx;
	}
	
	.add-detail-button .detail-row {
	  height: 10rpx;
	  width: 70rpx;
	  background-color: #fff;
	  position: absolute;
	  top: 47rpx;
	  left: 15rpx;
	}
	
	.add-detail-wrapper {
	  position: fixed;
	  bottom: 0;
	  /* left: 5%; */
	  z-index: 11;
	  height: 100%;
	  width: 100%;
	  background: rgba(14, 14, 14, 0.4);
	  /* box-shadow: 10rpx black; */
	}
	
	.cancel-add-detail {
	  position: absolute;
	  top: 0;
	  height: 40%;
	  width: 100%;
	}
	
	.add-detail {
	  position: absolute;
	  bottom: 0;
	  height: 60%;
	  width: 100%;
	  background-color: #fff;
	  border-top-left-radius: 45rpx;
	  border-top-right-radius: 45rpx;
	  border-top: 2rpx solid black;
	}
	
	/* animate------------------------------------- */
	/* ----------------------------------------------
		 * Generated by Animista on 2020-10-24 10:55:25
		 * Licensed under FreeBSD License.
		 * See http://animista.net/license for more info. 
		 * w: http://animista.net, t: @cssanimista
		 * ---------------------------------------------- */
	
	/**
		 * ----------------------------------------
		 * animation slide-top
		 * ----------------------------------------
		 */
	
	.slide-top {
	  -webkit-animation: slide-top 0.9s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
	  animation: slide-top 0.9s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
	}
	
	.slide-bottom {
	  -webkit-animation: slide-bottom 0.9s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
	  animation: slide-bottom 0.9s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
	}
	
	@-webkit-keyframes slide-top {
	  0% {
	    bottom: -800rpx;
	  }
	  100% {
	    bottom: 0rpx;
	  }
	}
	@keyframes slide-top {
	  0% {
	    bottom: -800rpx;
	  }
	  100% {
	    bottom: 0rpx;
	  }
	}
	
	@-webkit-keyframes slide-bottom {
	  0% {
	    bottom: 0rpx;
	  }
	  100% {
	    bottom: -800rpx;
	  }
	}
	@keyframes slide-bottom {
	  0% {
	    bottom: 0rpx;
	  }
	  100% {
	    bottom: -800rpx;
	  }
	}
	
</style>
